@import url(https://fonts.googleapis.com/css?family=Saira+Semi+Condensed);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #252525;
  /* overflow: hidden; */
  overflow: auto;
  background: #c0c1bf;
}

a,
a label button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: #252525;
}

.container {
  margin: 0 auto;
  padding: 10px;
  position: relative;
  min-height: 0px;

}

.box-categories {
  position: absolute;
  width: 100px;
  height: 100px;
  right: 5px;

}

.b1 {
  display: inline-block;
  position: relative;
  margin: 5px;
  float: left;
  width: calc(33% - 10px);
  min-height: 300px;
  margin-top: 12px;
}

.b2,
.b3,
.b4 {
  display: inline-block;
  position: relative;
  margin: 5px;
  float: left;
  margin-right: 20px;
  /* width: calc(33% - 10px); */
  min-height: 10px;
  margin-top: 12px;
}

.box-trending-stats {

  height: 100px;
  width: 100px;
  position: relative;
}

.box-posts {
  position: absolute;

  width: 100px;
  height: 100px;
  right: 5px;
}

.b1-trends {
  display: inline-block;
  position: relative;
  margin: 5px;
  float: left;
  width: calc(20% - 10px);
  min-height: 0px;
  margin-top: 12px;
}

.b2-trends {
  float: left;
  width: calc(66% - 10px);
  min-height: 300px;
  background-color: rgb(158, 158, 158);
  margin-top: 12px;
  padding: 4px;
  border-width: 0px;
}

.chartBoxTitle {
  color: #fff !important;
}


.btn {
  text-transform: uppercase;
  margin: 35px auto;
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, .29);
  cursor: pointer;
  background: #fff;
  font-size: 16px;
  width: 250px;
  position: relative;
}

.btn:hover {
  border-color: rgba(0, 0, 0, .5);
  text-decoration: none;
}

.btn:focus {
  outline: 0;
  font-weight: 700;
  border-width: 2px;
}

.center {
  text-align: center;
}

.active {
  font-weight: bold;
}

.nav ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  text-decoration: none;
}

.nav li:first-child {
  padding-left: 0;
}

ul {
  padding-left: 0;
}

li {
  list-style-type: none;
  padding: 10px;
  text-decoration: none;
}

.avatar {
  height: 50px;
  border-radius: 25px;
  margin: 10px;
}

.heart-button {
  background: transparent;
  border: none;
}

.textarea {
  border-radius: 4px;
  border: 1px solid #dad7d7;
  padding: 10px;
  font-size: 15px;
  height: 100px;
}

.card {
  height: 233px;
  transition: .3s ease;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
  width: 400px;
}

.card:hover {
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.8);
  -webkit-transform: translateY(-10px) scale(1.07);
          transform: translateY(-10px) scale(1.07);
}

.card:hover .entry-title {
  background-position: -100% 0;
}
* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
}

#notfound {
  position: relative;
  height: 100vh;
}

#notfound .notfound {
  position: absolute;
  left: 50%;
  top: 20%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.notfound {
  max-width: 410px;
  width: 100%;
  text-align: center;
}

.notfound .notfound-404 {
  height: 280px;
  position: relative;
  z-index: -1;
}

.notfound .notfound-404 h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 230px;
  margin: 0px;
  font-weight: 900;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  background: url(/static/media/bg.3644ee70.jpg) no-repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: cover;
  background-position: center;
}


.notfound h2 {
  font-family: 'Montserrat', sans-serif;
  color: #000;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 40;
}

.notfound p {
  font-family: 'Montserrat', sans-serif;
  color: #000;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 20px;
  margin-top: 0px;
}

.notfound a {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  background: #0046d5;
  display: inline-block;
  padding: 15px 30px;
  border-radius: 40px;
  color: #fff;
  font-weight: 700;
  box-shadow: 0px 4px 15px -5px #0046d5;
}


@media only screen and (max-width: 767px) {
  .notfound .notfound-404 {
    height: 142px;
  }

  .notfound .notfound-404 h1 {
    font-size: 112px;
  }
}
