span {
    display: inline-block;
    position: relative;
    margin-right: 5px;
}

span:after {
    content: "";
    position: absolute;
    height: 100%;
    width: 0;
    top: 0;
    left: 0;
    transition: 0.6s;
    z-index: -5;
}

