.main-container {
    position: absolute;
    top: 40%;
    left: 40%;
    transform: translate(-50%, -50%);
    color: #ffffff;
}

.first-container,
.second-container {
    cursor: pointer;
    position: relative;
}

.first-container h1,
.second-container h1 {
    position: relative;
    letter-spacing: 2px;
    font-size: 1.5vw;
}

.first-container {
    margin-bottom: 15px;
}

span {
    display: inline-block;
    position: relative;
    margin-right: 5px;
}

span:after {
    content: "";
    position: absolute;
    height: 100%;
    width: 0;
    top: 0;
    left: 0;
    transition: 0.6s;
    z-index: -5;
}

.share:hover>h1>span:nth-child(1):after {
    background: white;
    width: 100%;
    transition: 0.6s;
    transition-delay: 100ms;
    transform: rotate(-25deg);
}

.share:hover>h1>span:nth-child(1) {
    color: #39393B;
    transition-delay: 110ms;
}

